.loginScreen {
    position: relative;
    height: 100%;
    background: url(" ./netflix-background.webp") center no-repeat;
    background-size: cover;
}



.loginScreen-logo {

    position: fixed;
    left: 0;
    width: 150px;
    object-fit: contain;
    padding: 20px;

    cursor: pointer;
}


.loginScreen-gradient {

    width: 100%;
    z-index: 1;
    height: 100vh;
    background: rgba(0, 0, 0, 0.4);
    background-image: linear-gradient(to top,
            rgba(0, 0, 0, 0.8)0,
            rgba(0, 0, 0, 0)60%,
            rgba(0, 0, 0, 0.8)100%);
}



.loginScreen-button {

    position: fixed;
    right: 20px;
    top: 20px;
    padding: 10px 20px;
    font-size: 1rem;
    color: #fff;
    background-color: #e50914;
    font-weight: 600;
    border: none;
    cursor: pointer;
}






/* LoginScreen Body */

.loginScreen-body>h1 {

    font-size: 3.125rem;
    margin-bottom: 20px;
}

.loginScreen-body>h2 {

    font-size: 2rem;
    font-weight: 400;
    margin-bottom: 30px;
}

.loginScreen-body>h3 {

    font-size: 1.3rem;
    font-weight: 400;
}

.loginScreen-body {

    align-self: center;
    position: absolute;
    top: 20%;

    width: fit-content;
    z-index: 1;
    color: #fff;
    padding: 20px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    left: 0;
    right: 0;
}


























.loginScreen-input>form>input {
    padding: 10px;
    outline-width: 0;
    height: 30px;
    width: 30%;
    border: none;
    max-width: 600px;

}







.loginScreen-inputbtn {

    padding: 16px 20px;
    font-size: 1rem;
    color: #fff;
    background-color: #e50914;
    border: none;
    cursor: pointer;
    font-weight: 500;



}