.signupScreen {

    max-width: 400px;
    padding: 70px;
    margin-left: auto;
    margin-right: auto;
    background: rgba(0, 0, 0, 0.85);
}

.signupScreen>form {
    display: grid;
    flex-direction: column;

}























.signupScreen>form>h1 {

    text-align: left;
    margin-bottom: 20px;
    font-weight: 100;
}

.signupScreen>form>h4 {

    text-align: left;
    margin-top: 30px;
}


.signupScreen-text {
    color: gray;
}


.signupScreen-link:hover {
    cursor: pointer;
    text-decoration: underline;



}
























.signupScreen>form>input {

    outline-width: 0;
    height: 40px;
    margin-bottom: 14px;
    border-radius: 5px;
    border: none;
    padding: 5px 15px;
    width: 200px
}

.signupScreen>form>button {

    padding: 16px 20px;
    font-size: 1rem;
    color: #fff;
    background-color: #e50914;
    border-radius: 5px;
    border: none;
    font-weight: 600;
    cursor: pointer;
    margin-top: 20px;
}