.nav {

    position: fixed;
    top: 0;
    padding: 20px;
    width: 100px;
    height: 30px;
    z-index: 1;

    /* Animations */
    animation-timing-function: ease-in;
    transition: all 0.5s;


}


.nav-black {

    background-color: #111;
    width: 100%;
}











.nav-contents {

    display: flex;
    justify-content: space-between;

}

.nav-logo {

    cursor: pointer;
    position: fixed;
    left: 0;
    width: 80px;
    object-fit: contain;
    padding-left: 20px;



}

.nav-avatar {

    cursor: pointer;
    position: fixed;
    right: 20px;
    width: 30px;
    border-radius: 5px;



}